</template>

<script setup>
import { permissionList } from '@/api/permission'
import { watchSwitchLang } from '@/utils/i18n'
import { ref } from 'vue'

/**
 * 权限分级:
 * 1.一级权限为页面权限
 *  permissionMark 对应路由名称
 * 2.二级权限为功能权限
